import { useState, useEffect, useRef, useCallback } from "react";
import { toast } from "react-hot-toast";
import { copyToClipboard } from "../utils/clipboard";
import { useOrchestration } from "./useOrchestration";

/**
 * AI   UI    
 */
const processAIResponse = (result) => {
  if (!result || !result.result) {
    console.error("AI  :  ", result);
    return {
      id: "error-" + Date.now(),
      type: "assistant",
      content: "   .  .",
      timestamp: new Date(),
      isError: true,
    };
  }

  // LangChain  ,   content 
  const responseContent = result.result;

  return {
    id: "response-" + Date.now(),
    type: "assistant",
    content: responseContent,
    timestamp: new Date(),
    //   
    performance_metrics: result.performance_metrics,
    model_info: result.model_info,
  };
};

/**
 *     
 * @param {string} projectId -  ID
 * @param {string} projectName -  
 * @param {Array} promptCards -   
 * @returns {Object} -    
 */
export const useChat = (projectId, projectName, promptCards = []) => {
  const [messages, setMessages] = useState([]);
  const [inputValue, setInputValue] = useState("");
  const [copiedMessage, setCopiedMessage] = useState(null);
  const [streamingMessageId, setStreamingMessageId] = useState(null);

  const messagesEndRef = useRef(null);
  const inputRef = useRef(null);

  const {
    isExecuting: isGenerating,
    isStreaming,
    executeOrchestration,
    pollOrchestrationResult,
    resetOrchestration,
  } = useOrchestration(projectId);

  //     -  (  )
  useEffect(() => {
    setMessages([]); //   
  }, [projectName]);

  //     
  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const scrollToBottom = useCallback(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, []);

  /**
   *    
   */
  const handleStreamingResponse = useCallback(
    (chunk, metadata) => {
      setMessages((prev) => {
        const updatedMessages = [...prev];
        const streamingMsgIndex = updatedMessages.findIndex(
          (msg) => msg.id === streamingMessageId
        );

        if (streamingMsgIndex !== -1) {
          //    
          updatedMessages[streamingMsgIndex] = {
            ...updatedMessages[streamingMsgIndex],
            content: updatedMessages[streamingMsgIndex].content + chunk,
            isLoading: true,
            isStreaming: true,
          };
        }

        return updatedMessages;
      });

      //  
      scrollToBottom();
    },
    [streamingMessageId, scrollToBottom]
  );

  /**
   *    
   */
  const handleStreamingComplete = useCallback(
    (result) => {
      setMessages((prev) => {
        const updatedMessages = [...prev];
        const streamingMsgIndex = updatedMessages.findIndex(
          (msg) => msg.id === streamingMessageId
        );

        if (streamingMsgIndex !== -1) {
          //    
          updatedMessages[streamingMsgIndex] = {
            ...updatedMessages[streamingMsgIndex],
            content: result.result,
            isLoading: false,
            isStreaming: false,
            performance_metrics: result.performance_metrics,
            model_info: result.model_info,
            timestamp: new Date(),
          };
        }

        return updatedMessages;
      });

      //  ID 
      setStreamingMessageId(null);

      //  
      scrollToBottom();
    },
    [streamingMessageId, scrollToBottom]
  );

  /**
   *  
   */
  const handleSendMessage = useCallback(async () => {
    if (!inputValue.trim() || isGenerating) return;

    const userMessage = {
      id: "user-" + Date.now(),
      type: "user",
      content: inputValue.trim(),
      timestamp: new Date(),
    };

    //   ID 
    const streamMsgId = "streaming-" + Date.now();
    setStreamingMessageId(streamMsgId);

    //     
    const streamingMessage = {
      id: streamMsgId,
      type: "assistant",
      content: "",
      timestamp: new Date(),
      isLoading: true,
      isStreaming: true,
    };

    setMessages((prev) => [...prev, userMessage, streamingMessage]);
    setInputValue("");

    try {
      const orchestrationData = {
        userInput: userMessage.content,
        chat_history: messages
          .filter((msg) => !msg.isLoading && !msg.isError)
          .map((msg) => ({
            role: msg.type === "user" ? "user" : "assistant",
            content: msg.content,
          })),
      };

      //   
      const streamingOptions = {
        useStreaming: true,
        chat_history: orchestrationData.chat_history,
        onChunk: handleStreamingResponse,
        onError: (error) => {
          console.error(" :", error);

          //   
          setMessages((prev) => {
            const updatedMessages = [...prev];
            const streamingMsgIndex = updatedMessages.findIndex(
              (msg) => msg.id === streamingMessageId
            );

            if (streamingMsgIndex !== -1) {
              updatedMessages[streamingMsgIndex] = {
                ...updatedMessages[streamingMsgIndex],
                content:
                  "    .  .",
                isLoading: false,
                isStreaming: false,
                isError: true,
                timestamp: new Date(),
              };
            }

            return updatedMessages;
          });

          setStreamingMessageId(null);
        },
        onComplete: handleStreamingComplete,
      };

      //   
      await executeOrchestration(userMessage.content, streamingOptions);

      //  pollOrchestrationResult   
      //    
    } catch (error) {
      console.error("  :", error);

      //   
      const errorType = error.code === "ECONNABORTED" ? "timeout" : "general";
      const errorMessage = {
        id: "error-" + Date.now(),
        type: "assistant",
        content:
          errorType === "timeout"
            ? "  .       .    ."
            : "    .     .",
        timestamp: new Date(),
        isError: true,
        errorDetails: {
          type: errorType,
          message: error.message,
          status: error.response?.status,
        },
      };

      setMessages((prev) => {
        //    
        const filteredMessages = prev.filter(
          (msg) => msg.id !== streamingMessageId
        );
        return [...filteredMessages, errorMessage];
      });

      setStreamingMessageId(null);
    }
  }, [
    inputValue,
    isGenerating,
    executeOrchestration,
    handleStreamingResponse,
    handleStreamingComplete,
    streamingMessageId,
    messages,
  ]);

  /**
   * Enter  
   */
  const handleKeyPress = useCallback(
    (e) => {
      if (e.key === "Enter" && !e.shiftKey) {
        e.preventDefault();
        handleSendMessage();
      }
    },
    [handleSendMessage]
  );

  /**
   *  
   */
  const handleCopyMessage = useCallback(async (content, messageId) => {
    const success = await copyToClipboard(content);
    if (success) {
      setCopiedMessage(messageId);
      setTimeout(() => setCopiedMessage(null), 2000);
    }
  }, []);

  /**
   *   
   */
  const handleCopyTitle = useCallback(async (title, messageId, index) => {
    const success = await copyToClipboard(title, "!");
    if (success) {
      setCopiedMessage(`${messageId}_title_${index}`);
      setTimeout(() => setCopiedMessage(null), 2000);
    }
  }, []);

  /**
   *  
   */
  const resetChat = useCallback(() => {
    setMessages([]);
    setInputValue("");
    setCopiedMessage(null);
    setStreamingMessageId(null);
    resetOrchestration();
  }, [resetOrchestration]);

  return {
    messages,
    inputValue,
    setInputValue,
    copiedMessage,
    isGenerating,
    isStreaming,
    streamingMessageId,
    messagesEndRef,
    inputRef,
    handleSendMessage,
    handleKeyPress,
    handleCopyMessage,
    handleCopyTitle,
    resetChat,
    scrollToBottom,
  };
};
